document.addEventListener('DOMContentLoaded', () => {
  const btnBurger = document.querySelector('.burger') as HTMLElement | null;
  const nav = document.querySelector('.main-nav') as HTMLElement | null;
  const btnLogin = document.querySelector('#login-btn') as HTMLElement | null;
  const authModal = document.querySelector('.auth') as HTMLElement | null;

  function closeIfClickedOutside(target: Node, elements: HTMLElement[], classToRemove: string) {
    if (elements.every(el => !el.contains(target))) {
      elements.forEach(el => el.classList.remove(classToRemove));
    }
  }

  if (btnBurger && nav) {
    btnBurger.onclick = () => {
      btnBurger.classList.toggle('burger--active');
      nav.classList.toggle('main-nav--active');
    }
  }

  if (btnLogin && authModal) {
    btnLogin.onclick = () => {
      authModal.classList.add('auth--active');
    }
  }

  document.addEventListener('click', (event) => {
    const target = event.target as Node;
    closeIfClickedOutside(target, [btnBurger, nav], 'burger--active');
    closeIfClickedOutside(target, [nav], 'main-nav--active');
    closeIfClickedOutside(target, [authModal], 'auth--active');
  });
});